section * {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

#container-slider {
  width: 100%;
  display: block;
  position: relative;
}

#slider {
  width: 100%;
  height: 100vh;
  min-height: 500px;
  display: block;
  position: relative;
}

#slider li {
  -o-transition: cubic-bezier(1, 0, 0, 1) 2s;
  z-index: -1;
  opacity: 0;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 100%;
  transition: all 2s cubic-bezier(1, 0, 0, 1);
  display: block;
  position: absolute !important;
  top: 0 !important;
  left: 0 !important;
}

#container-slider .arrowPrev, #container-slider .arrowNext {
  color: #cccccca6;
  cursor: pointer;
  z-index: 2;
  font-size: 30pt;
  position: absolute;
  top: 50%;
  left: 50px;
}

#container-slider .arrowNext {
  left: initial;
  right: 50px !important;
}

.content_slider {
  color: #fff;
  width: 100%;
  height: 100%;
  padding: 15px 30px;
}

.content_slider div {
  text-align: center;
}

.content_slider h2 {
  letter-spacing: 1px;
  text-transform: uppercase;
  margin-bottom: 20px;
  font-family: arial;
  font-size: 30pt;
}

.content_slider p {
  color: #fff;
  max-width: 1024px;
  margin-bottom: 20px;
  font-family: arial;
  font-size: 15pt;
}

#slider li .content_slider {
  background: #00000080;
  padding: 10px 125px;
}

.content_slider {
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  justify-content: center;
  align-items: center;
  display: flex;
}

.btnSlider {
  color: #fff;
  letter-spacing: 1px;
  background: #0d0d0d8c;
  border: 1px solid #ccc;
  border-radius: 31px;
  padding: 10px 50px;
  font-family: arial;
  font-size: 15pt;
  text-decoration: none;
  transition: all .5s;
}

.btnSlider:hover {
  background: #111;
  border: 1px solid #111;
}

.listslider {
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  z-index: 2;
  justify-content: space-between;
  align-items: center;
  list-style: none;
  display: flex;
  position: absolute;
  bottom: 5%;
  left: 50%;
  transform: translateX(-50%);
}

.listslider li {
  cursor: pointer;
  border-radius: 50%;
  width: 10px;
  height: 10px;
  margin: 0 5px;
}

.listslider li a {
  background: #ccc;
  border-radius: 50%;
  width: 100%;
  height: 100%;
  display: block;
}

.item-select-slid {
  background: #fff !important;
}

.slider_activa {
  z-index: 1;
  opacity: 1 !important;
}

.slider_inactiva {
  z-index: 0;
  opacity: 0 !important;
}

